import request from '@/utils/request'

const baseUrl = '/api/admin/visit'

const visitApi = {}

visitApi.count7Day = () => {
  return request({
    url: `${baseUrl}/count7Day`,
    params: {
      
    },
    method: 'get'
  })
}

visitApi.terminalCount = () => {
  return request({
    url: `${baseUrl}/terminalCount`,
    params: {
      
    },
    method: 'get'
  })
}

visitApi.countVisit = () => {
  return request({
    url: `${baseUrl}/countVisit`,
    params: {
      
    },
    method: 'get'
  })
}

visitApi.countTodayCityVisit = () => {
  return request({
    url: `${baseUrl}/countTodayCityVisit`,
    params: {
      
    },
    method: 'get'
  })
}

visitApi.countOS = () => {
  return request({
    url: `${baseUrl}/countOS`,
    params: {
      
    },
    method: 'get'
  })
}

visitApi.page = (params) => {
  return request({
    url: `${baseUrl}/page`,
    params: params,
    method: 'get'
  })
}

export default visitApi
