<template>
  <div id="china_chart" style="height:400px;"></div>
</template>

<script>
import echarts from "echarts";
import visitApi from "@/api/visitApi";
import cityJson from "@/json/city";
import china from "echarts/map/json/china.json";
echarts.registerMap("china", china);
export default {
  data() {
    return {};
  },
  mounted() {
    this.initChart();
    let that = this;
    let interval = setInterval(() => {
      this.initChart();
      if (that.$route.path != "/index") {
        clearInterval(interval);
      }
    }, 5000);
  },
  methods: {
    initChart: function() {
      var dom = document.getElementById("china_chart");
      if (!dom) {
        return false;
      }
      var myChart = echarts.init(dom);
      var geoCoordMap = {};
      cityJson.map(pro => {
        var city = pro.children;
        for (var i = 0; i < city.length; i++) {
          var citydetail = new Array();
          var name = city[i].name;
          geoCoordMap[name + "市"] = citydetail;
          var lat = parseFloat(city[i].lat);
          var log = parseFloat(city[i].log);
          citydetail.push(log);
          citydetail.push(lat);
        }
      });
      var convertData = function(data) {
        var res = [];
        for (var i = 0; i < data.length; i++) {
          var geoCoord = geoCoordMap[data[i].name];
          if (geoCoord) {
            res.push({
              name: data[i].name,
              value: geoCoord.concat(data[i].PV).concat(data[i].UV)
            });
          }
        }
        return res;
      };
      visitApi.countTodayCityVisit().then(res => {
        let cityData = res.data;
        var option = {
          title: {
            text: "今日访问城市来源",
            left: "left"
          },
          tooltip: {
            trigger: "item",
            formatter: function(data) {
              return (
                data.name +
                "<br>浏览量：" +
                data.value[2] +
                "<br>访问量：" +
                data.value[3]
              );
            }
          },
          geo: {
            map: "china",
            roam: true,
            label: {
              normal: {
                show: false,
                textStyle: {
                  color: "rgba(0,0,0,0.4)"
                }
              }
            },
            normal: {
              borderColor: "rgba(0, 0, 0, 0.2)"
            },
            emphasis: {
              areaColor: null,
              shadowOffsetX: 0,
              shadowOffsetY: 0,
              shadowBlur: 20,
              borderWidth: 0,
              shadowColor: "rgba(0, 0, 0, 0.5)"
            }
          },
          series: [
            {
              name: "城市",
              type: "effectScatter",
              coordinateSystem: "geo",
              symbolSize: function(val) {
                return val[2] * 1;
              },
              showEffectOn: "render",
              rippleEffect: {
                brushType: "stroke"
              },
              hoverAnimation: true,
              itemStyle: {
                normal: {
                  color: "#f75749",
                  shadowBlur: 10,
                  shadowColor: "#333"
                }
              },
              zlevel: 1
            }
          ]
        };
        option.series[0].data = convertData(cityData);
        myChart.setOption(option);
        window.addEventListener("resize", function() {
          myChart.resize();
        });
      });
    }
  }
};
</script>

